.footer {
	margin: 100px 0;
}
.contacts__form {
	padding: 75px 0;
}
.form {
	display: flex;
	gap: 35px;
}
.form__label {
	display: block;
	padding-top: 25px;
}
.input-tel {
	width: 100%;
	color: #fff;
	background-color: transparent;
	font-size: 50px;
	font-weight: 400;
	font-family: "Kharkiv";
}

.control-wrapper {
	flex: 1 1 auto;
	border-bottom: solid #fff 2px;
}
.contacts__button {
	padding: 40px 55px;
	color: #fff;
	background-color: transparent;

	border: solid #fff 2px;
	background-color: transparent;
	font-size: 14px;
	text-transform: uppercase;
	transition: 0.3s ease border;
	&:hover {
		border: $red-accent 2px solid;
	}
}

.contacts__contact {
	display: flex;
	justify-content: space-between;
}

.contacts__descr {
	flex: 0 0 410px;

	color: #cbcbcb;
	font-size: 20px;
	line-height: 1.55;
}

.contacts__seti {
	display: flex;
	align-items: flex-end;
	gap: 60px;

	& a {
		border-bottom: solid $red-accent 1px;
		color: #cbcbcb;
		font-size: 20px;
		text-transform: uppercase;
	}
}
@media (max-width: 1225px) {
	.contacts__form{
		padding: 0 0 20px 0;
	}
}
@media (max-width: 1025px) {
	.contacts__contact {
		flex-direction: column;
		gap: 50px;
	}
	
	.contacts__descr {
		max-width: 70%;
		flex: 0 0 auto;
	}
}

@media (max-width: 900px) {
	.footer{
		margin-top: 60px;
	}
	
	.form {
		flex-direction: column;
	}
	.input-tel {
		font-size: clamp(2.125rem, 1.325rem + 3.2vw, 3.5rem);
	}
	.contacts__descr {
		max-width: 100%;
		flex: 0 0 auto;
	}
}


@media (max-width: 640px) {
	.contacts__seti {
		flex-direction: column;
		align-items: flex-start;
		gap: 30px;
	}



}