$fontFamily: "TildaSans";
//+++++++++++Шрифт по умолчанию============================================
//</ШРИФТЫ>================================================================

//<ПЕРЕМЕННЫЕ>=============================================================
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1160;

$white: #fff;
$bg-body: #000;
$red-accent: #f21100;
$grey: #d9d9d9;

//</ПЕРЕМЕННЫЕ>============================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>=============================================
@import "help/null";

body {
	overflow-x: hidden;
	color: $white;
	background-color: $bg-body;
	font-family: $fontFamily, Arial, sans-serif;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>============================================

//<ОБОЛОЧКА>================================================================
.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}
//</ОБОЛОЧКА>===============================================================

//</ОСНОВНАЯ СЕТКА>=========================================================
._container {
	max-width: $maxWidthContainer + px;
	width: 100%;
	padding: 0 15px;
	margin: 0 auto;
}

@media (max-width: 1025px) {
	._container {
		max-width: 900px;
	}
}

@media (max-width: 900px) {
	._container {
		max-width: 600px;
	}
}

//</ОСНОВНАЯ СЕТКА>=========================================================

//<ЭЛЕМЕНТЫ>================================================================
h2,
h3,
h4 {
	text-transform: uppercase;
	font-size: 28px;
	line-height: 34px;
	margin-bottom: 40px;
	@media (min-width: 961px) {
		font-size: 36px;
	}
}

h5 {
	font-weight: 600;
	font-size: 24px;
	line-height: 29px;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: #ffffff;
	margin-bottom: 22px;
}

//<ЭЛЕМЕНТЫ>================================================================

