.title-page {
	font-family: "Kharkiv";
	letter-spacing: 1.5px;
	font-size: clamp(34px, -0.375rem + 8.33vw, 94px);
	font-weight: 700;
}

.title-page_second {
	line-height: 1;
	position: relative;
	font-size: clamp(24px, -0.375rem + 8.33vw, 94px);
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		left: 30px;
		right: 0;
		bottom: -5px;
		height: 40px;
		width: 560px;
		background-color: $red-accent;
	}
	& span {
		position: absolute;
		right: 6px;
		top: 0px;
		font-family: $fontFamily;
		font-size: 14px;
		&::before {
			content: "";
			position: absolute;
			top: 0;
			right: 0;
			z-index: 5;
			width: 50px;
			height: 50px;
			border-radius: 50%;
			color: red;
		}
	}
}


.title-page_multiline {
	line-height: 100px;
	& span {
		line-height: 5px;
	}
}
@media (max-width: 961px) {
	.title-page_second {
		&::after {
			height: 25px;
			width: 30%;
			left: -8px;
		}
		& span {
			top: -10px;
		}
	}

	.title-page_equipment {
		& span {
			top: -25px;
			right: 0;
		}
	}
}

@media (max-width: 900px) {
	.title-page_multiline {
		line-height: 55px;
	}
	.title-page_equipment {
		font-size: clamp(34px, -0.375rem + 8.33vw, 50px);
	}
}

.link-section {
	position: relative;
	padding-bottom: 8px;
	border-bottom: solid $red-accent 2px;
	max-width: 260px;
	width: 100%;
	font-size: 20px;
	line-height: 1.55;
	font-weight: 400;
	text-transform: uppercase;
	color: $white;
	transition: color 0.3s ease-in-out;
	&::after {
		content: "";
		position: absolute;
		right: 0;
		top: 7px;
		width: 16px;
		height: 16px;
		background-image: url(../img/icons/arrow.svg);
		background-repeat: no-repeat;
		background-position: center;
	}
	&:hover {
		color: $red-accent;
	}
}

.about {
	padding: 75px 0 100px;
}
.about__title {
	position: relative;
	&::after {
		content: "";
		position: absolute;
		z-index: -1;
		left: -20px;
		right: 0;
		bottom: -15px;
		height: 30px;
		width: 100%;
		max-width: 390px;
		background-color: $red-accent;
	}
}

@media (min-width: 1025px) {
	.about__title {
		&::after {
			max-width: 630px;
			height: 40px;
			left: calc(100% - 630px);
		}
	}
}

.about__content {
	display: flex;
	flex-direction: column;
	row-gap: 110px;
	margin: 75px 0 90px;
}

@media (min-width: 1025px) {
	.about__content {
		flex-direction: row;
		column-gap: 100px;
	}
}

@media (max-width: 1025px) {
	.about__content {
		margin: 50px 0 0;
		gap: 70px;
	}
}

@media (max-width: 600px) {
	.about__content {
		gap: 50px;
	}
}

.about__text {
	max-width: 500px;
	color: $grey;
	font-size: clamp(1rem, 0.905rem + 0.48vw, 1.25rem);
	line-height: 25px;
}

