.portfolio {
	margin-top: 100px;
}

.portfolio__filter-list {
	margin: 100px 0 70px;
}

.filter-list {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
}

.filter-list__btn {
	padding: 6px 18px;
	color: #fff;
	background-color: transparent;
	border: solid $red-accent 1px;
	font-size: 20px;
	font-weight: 400;
	text-transform: uppercase;
}

.product {
	max-width: 1160px;
	margin-bottom: 30px;
	padding-bottom: 25px;
	border-bottom: solid red 2px;
}

.grid__item {
	width: 100%;
	display: flex;
	gap: 20px;
}

.grid__item-img {
	position: relative;
	flex: 0 0 660px;
	height: 300px;
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.grid__item-title {
	font-size: 30px;
	text-transform: uppercase;
}

.grid__item-text {
	margin: 25px 0 20px;
	color: #bdbdbd;
	font-size: 20px;
	font-weight: 300;
	line-height: 1.55;
}

.grid__item-info {
	display: flex;
	gap: 15px;
	color: #bdbdbd;
	font-size: 10px;
	letter-spacing: 1px;
	text-transform: uppercase;
}

@media (max-width: 1025px) {
	.grid__item {
		flex-direction: column;
	}
	.grid__item-img {
		flex: 0 0 300px;
	}

	.grid__item-text-block {
		flex: 0 0 auto;
	}
	.portfolio__filter-list {
		margin: 70px 0 40px;
	}
}

// .advantages////////////////////////////////////

.advantages {
	margin-top: 100px;
}

.advantages__wrap {
	display: flex;
	flex-wrap: wrap;
}
.advantages__block {
	display: flex;
	flex: 0 0 30%;
}
.advantages__block_big {
	flex: 0 0 60%;
}
.advantages__block_lg {
	position: relative;
	flex: 0 0 30%;
	&::before {
		content: "";
		position: absolute;
		z-index: 2;
		left: -50px;
		top: 0;
		height: 200%;
		width: 1px;
		background-color: $red-accent;
	}
}

.advantages__block_sm {
	flex: 0 0 30%;
	border-top: solid $red-accent 1px;
}
.advantages__block_line {
	padding-left: 24px;
	border-left: solid $red-accent 1px;
}
.advantages__block_day {
	flex: 1 0 30%;
}

.advantages__block-title {
	font-weight: 700;
	letter-spacing: 1.5px;
}

.advantages__block-title_lg {
	flex: 0 0 255px;
	margin-right: 50px;
	font-size: 90px;
	line-height: 90px;
}
.advantages__block-title_sm {
	margin-right: 20px;
	font-size: 30px;
	line-height: 90px;
}

.advantages__block-descr {
	align-self: center;
	max-width: 265px;
	color: #bdbdbd;
	font-size: 24px;
	font-weight: 400;
}

@media (max-width: 1180px) {
	.advantages {
		margin-top: 50px;
	}
	.advantages__block-title_lg {
		font-size: 70px;
		margin-right: 10px;
		flex: 0 0 200px;
	}
}

@media (max-width: 900px) {
	.advantages__block {
		flex-direction: column;
	}
	.advantages__block_lg {
		flex-direction: column;
	}

	.advantages__block_lg {
		align-self: auto;
		&::before {
			left: -50px;
			top: 0;
			height: 100%;
			width: 1px;
			background-color: #f21100;
		}
	}
	.advantages__block_sm.advantages__block_day {
		flex: 0 0 100%;
	}

	.advantages__block-title_lg {
		flex: 0 0 100px;
	}
	.advantages__block-descr {
		align-self: auto;
		margin-bottom: 40px;
	}
	.advantages__block_sm {
		border-left: none;
		flex: 1 0 30%;
	}
	.advantages__block-title_sm {
		font-size: 45px;
	}
}
@media (max-width: 641px) {
	.advantages__block-title_lg {
		font-size: 50px;
	}
	.advantages__block-descr {
		font-size: 20px;
		&_first {
			max-width: calc(100% - 30px);
		}
	}

	.advantages__block_big {
		flex: 0 0 250px;
	}
	.advantages__block_lg::before {
		left: -27px;
	}
}

// .algorithm////////////////////////////////////////////////////////////
.algorithm {
	margin-top: 100px;
	margin-bottom: 100px;
}
.algorithm__list {
	margin-top: 90px;
}
.algorithm__item {
	display: flex;
	gap: 40px;
	&:not(:last-child) {
		margin-bottom: 90px;
	}
	&_third {
		flex-wrap: wrap;
	}
}

.algorithm__text {
	color: #bdbdbd;
	font-size: 20px;
	line-height: 1.55;
	font-weight: 400;
	&_first {
		flex: 0 0 265px;
	}
	&_third {
		flex: 0 0 665px;
	}
}

.algorithm__img {
	position: relative;
	flex: 0 0 300px;
	overflow: hidden;
	&_first {
		flex: 0 0 360px;
		align-items: stretch;
		min-height: 180px;
	}
	&_second {
		flex: 0 0 665px;
		align-items: stretch;
		min-height: 300px;
	}
	&_third {
		flex: 0 0 100%;
		min-height: 380px;
	}
	& img {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}

.algorithm__title {
	font-size: 30px;
	&_second {
		margin-bottom: 30px;
	}
}

@media (max-width: 1025px) {
	.algorithm {
		margin-top: 50px;
		
	}
	.algorithm__list{
		margin-top: 50px;
	}
	.algorithm__text {
		&_first {
			flex: 0 0 370px;
		}
		&_third {
			flex: 0 0 665px;
		}
	}
	.algorithm__img {
		&_first {
			flex: 0 0 300px;
		}
		&_second {
			flex: 0 0 400px;
			min-height: 200px;
		}
		&_third {
			min-height: 300px;
		}
	}
	.algorithm__title {
		flex: 0 0 90px;
	}
}

@media (max-width: 900px) {
	.algorithm__item {
		flex-direction: column;
		row-gap: 20px;
		&:not(:last-child) {
			margin-bottom: 60px;
		}
		&_third {
			flex-direction: column-reverse;
		}
	}

	.algorithm__text {
		flex: auto;
	}
	.algorithm__img {
		flex: 0 0 200px;
		min-height: 200px;
		&_first {
			order: -2;
			flex: auto;
		}
	}
	.algorithm__title {
		flex: auto;
		&_first {
			order: -1;
		}
	}
}

