// equipment//////////////////////////////////////////////////////////////////
.equipment {
	margin: 100px 0;
}

.equipment-item {
	margin-top: 100px;
	display: flex;
	justify-content: space-between;
}

.equipment-item__number {
	position: relative;
	flex: 0 1 270px;
	align-self: flex-start;
	overflow: hidden;
	padding-top: 25px;
	font-size: 20px;
	font-weight: 400;
	&::after {
		content: "";
		position: absolute;
		right: 0px;
		top: 32px;
		width: 200px;
		height: 5px;
		background-image: url(../img/icons/dots.svg);
		background-repeat: no-repeat;
		background-size: contain;
	}
}

.equipment-item__data {
	flex: 0 0 480px;
}

.equipment-item__title {
	color: #eeeeee;
	font-size: 50px;
	line-height: 1;
	font-weight: 400;
}

.equipment-item__descr {
	margin-top: 20px;
	color: #bdbdbd;
	font-size: 20px;
	line-height: 1.55;
	line-height: 25px;
}

.equipment-item__link {
	align-self: flex-start;
	position: relative;
	padding-top: 18px;
	padding-right: 80px;
	text-transform: uppercase;
	color: #fff;
	font-size: 20px;
	line-height: 1.55;
	font-weight: 400;
	&::after {
		content: "";
		position: absolute;
		bottom: -5px;
		left: 0;
		width: 100%;
		height: 1px;
		background-color: $red-accent;
	}
	&::before {
		content: "";
		position: absolute;
		right: 0px;
		top: 25px;
		width: 15px;
		height: 15px;
		background-image: url(../img/icons/arrow-up.svg);
		background-size: contain;
		background-repeat: no-repeat;
		background-position: center;
	}
}

.equipment-item__link-img {
	margin-left: 100px;
	padding-top: 8px;
}

@media (max-width: 1025px) {
	.equipment-item {
		gap: 30px;
	}

	.equipment-item__number {
		&::after {
			right: -45px;
		}
	}

	.equipment-item__data {
		flex: 0 0 400px;
	}
	.equipment-item__title {
		font-size: clamp(24px, -0.375rem + 8.33vw, 40px);
	}
}

@media (max-width: 961px) {
	.equipment-item {
		flex-direction: column;
		gap: 15px;
		margin-top: 50px;
	}
	.equipment-item__number,
	.equipment-item__data,
	.equipment-item__link {
		flex: auto;
	}
	.equipment-item__number {
		width: 200px;
	}
}

