// .faq //////////////////////////////////////////////
.faq {
	margin: 100px 0;
}

.accordion {
	border-top: solid #fff 1px;
	transition: transform 0.3s ease-in-out;
	padding: 25px 0;
	&:last-child {
		border-bottom: solid #fff 1px;
	}
}

.faq__block {
	max-width: 100%;
}

.accordion__control {
	
	user-select: none; 
	display: flex;
	width: 100%;
	justify-content: space-between;
	background-color: transparent;
	cursor: pointer;
	&:hover .accordion__icon {
		opacity: 1;
	}
}

.accordion__content {
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	will-change: max-height;
	transition: all 0.3s ease-out;
	font-size: 20px;
	line-height: 1.65;
	color: #bdbdbd;
	max-width: 650px;
	& p {
		padding-bottom: 0;
	}
}

.accordion__title {
	
	user-select: none; 
	text-align: left;
	flex: 0 0 90%;
	color: #fff;
	font-size: 30px;
	text-transform: uppercase;
}

.accordion__icon {
	position: relative;
	width: 36px;
	height: 36px;
	border-radius: 50%;
	background-color: $red-accent;
	transition: all 0.3s ease-in-out;
	&::before,
	&::after {
		content: "";
		position: absolute;
		z-index: 3;
		background-color: #fff;
	}
	&::before {
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: 1px;
		height: 21px;
	}
	&::after {
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		width: 21px;
		height: 1px;
	}
}

.is-open .accordion__icon {
	transform: rotate(45deg);
}

.is-open .accordion__content {
	opacity: 1;
}

@media (max-width: 961px) {
	.accordion__title {
		font-size: 25px;
		line-height: 39px;
	}
}
