.header {
	position: relative;
	height: 300px;
	&::before {
		content: "";
		position: absolute;
		z-index: -1;
		width: 100%;
		height: 100%;
		background-image: url(../img/header-bg.jpg);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		filter: blur(2px);
	}
	&::after {
		content: "";
		position: absolute;
		top: 101px;
		left: 50%;
		width: calc(100% - 10px);
		max-width: 1400px;
		height: 2px;
		transform: translateX(-50%);
		background-color: $white;
	}
}

.header__content {
	height: 100px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header__logo a {
	font-size: 34px;
	color: $white;
}

.header__phone a {
	color: $white;
	font-size: 16px;
	font-weight: 600;
	transition: color 0.3s ease-in-out;
	&:hover {
		color: #b8b8b8;
	}
}
@media (min-width: 641px) {
	.header {
		height: 350px;
	}
}

@media (min-width: 1025px) {
	.header {
		height: 410px;
	}
}

@media (max-width: 961px) {
	.header__phone {
		display: none;
	}
}
