.nav {
	& ul {
		display: flex;
		align-items: center;
	}
	& li:not(:last-child) {
		margin-right: 70px;
	}
	& a {
		color: $white;
		font-size: 16px;
		line-height: 1;
		font-family: $fontFamily;
		font-weight: 600;
		text-transform: uppercase;
		transition: color 0.3s ease-in-out;
		&:hover {
			color: #b8b8b8;
		}
	}
}

.burger {
	position: relative;
	z-index: 101;
	width: 50px;
	height: 50px;
	display: none;
	border: none;
	padding: 0px;
	background-color: $red-accent;
	cursor: pointer;

	&__line {
		background-color: #fff;
		position: absolute;
		height: 2px;
		width: 20px;
		right: 11px;
		top: 50%;
		transform: translateY(-50%);
		transition: opacity 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
	}
	&::before,
	&::after {
		content: "";
		position: absolute;
		left: 50%;
		width: 30px;
		height: 2px;
		transform: translateX(-50%);
		transition:
			transform 0.3s ease-in-out,
			top 0.3s ease-in-out,
			bottom 0.3s ease-in-out;
		background-color: #fff;
	}
	&::before {
		top: 13px;
	}
	&::after {
		bottom: 13px;
	}
	&:hover .burger__line {
		right: 20px;
	}
}

