.stop--scroll {
	overflow: hidden;
}

@media (max-width: 961px) {
	.nav {
		position: fixed;
		z-index: 100;
		left: 0;
		top: 0;
		width: 100%;
		height: 100vh;
		overflow-y: auto;
		padding: 90px 30px 30px 30px;
		background-color: rgba(0, 0, 0, 1);
		transform: translateX(100%);
		transition: transform 0.6s ease-in-out;
		& ul {
			flex-direction: column;
			align-items: flex-start;
		}
		& li {
			margin-bottom: 30px;
			margin-right: 20px;
		}
		& a {
			font-size: 24px;
		}
		& li:not(:last-child) {
			margin-right: 20px;
		}
	}
	.nav--visible {
		transform: none;
	}
	.burger {
		display: block;
	}
}
